<template>
  <div id="app" class="container">
    <div class="page-header">
      <h1>Vue.js 1 & Firebase Sample Application</h1>
    </div>

    <div class="panel panel-default">
      <div class="panel-heading">
        <h3>Add Book</h3>
      </div>
      <div class="panel-body">
        <form class="form-inline" @submit.prevent="addBook">
          <div class="form-group">
            <input type="text" class="form-control" id="bookTitle" placeholder="book title" v-model="newBook.title">
          </div>
          <div class="form-group">
            <input type="text" class="form-control" id="" placeholder="author name" v-model="newBook.author">
          </div>
          <div class="form-group">
            <input type="text" class="form-control" id="" placeholder="url" v-model="newBook.url">
          </div>
          <input type="submit" value="Add Book" class="btn btn-primary form-control">
        </form>
      </div>
    </div>

    <div class="panel panel-default">
      <div class="panel-heading">
        <h3>Books Lists</h3>
      </div>
      <div class="panel-body">
        <table class="table table-striped">
          <thead>
            <tr>
              <th>Title</th>
              <th>Author</th>
              <th>Deletion</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="book in books">
              <td><a v-bind:href="book.url">{{book.author}}</a></td>
              <td>{{book.title}}</td>
              <td>
                <a href="" @click.prevent="removeBook(book)">delete</a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'

import Firebase from 'firebase'
import toastr from 'toastr'

let config = {
    apiKey: "AIzaSyCtZxCaT7ahF9zzSe5Xu3-8HDA9eJQXI1Q",
    authDomain: "realtimetodoapp.firebaseapp.com",
    databaseURL: "https://realtimetodoapp.firebaseio.com",
    projectId: "realtimetodoapp",
    storageBucket: "realtimetodoapp.appspot.com",
    messagingSenderId: "1023524875669"
}

let app = Firebase.initializeApp(config);
let db = app.database();

let booksRef = db.ref('books');

export default {
  name: 'app',
  firebase: {
    books: booksRef
  },
  data(){
    return {
      newBook: {
        title: '',
        author: '',
        url: ''
      }
    }
  },
  methods: {
    addBook: function(){
      booksRef.push(this.newBook);
      toastr.success("Book added");
      this.newBook.title = '';
      this.newBook.author = '';
      this.newBook.url = '';
    },
    removeBook: function(book){
      booksRef.child(book['.key']).remove();
      toastr.success("Book removed");
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



